body {
    text-align: center;
    background-color: #0d0101;
    color: white;
    font-family: Arial, sans-serif;
  }
  
  .game-container {
    margin-top: 5px;
  }
  
  canvas {
    background: #444;
    display: block;
    margin: auto;
    border: 2px solid white;
  }
  
  button {
    padding: 10px 20px;
    font-size: 20px;
    margin-top: 20px;
    cursor: pointer;
  }

  .game-over {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    background: rgba(0, 0, 0, 0.8);
    padding: 20px;
    border-radius: 10px;
  }
  
  .game-over button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    background: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
  }
  
  .game-over button:hover {
    background: #45a049;
  }